.buttonsContainer {
  display: flex;
  justify-content: space-between;
  margin: 30px;
}
.button {
  outline: 2px solid rgb(23, 81, 114);
  border: none;
  background-color: rgb(89, 147, 180);
  border-radius: 3px;
  color: #ffffff;
}
.button:not(:last-child) {
  margin-right: 20px;
}
