.feedback {
  width: 340px;
  margin: 50px auto;
  padding-top: 50px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 0 0 5px rgb(100, 129, 145);
  box-shadow: 0 0 5px rgb(68, 82, 90);
}
